<template>
  <div class="table-head">
      <h3 class="top-header content-position bg-color box-shadow">最新票房排行榜</h3>
      <ul class="row list-unstyled list-header content-position bg-color">
          <li class="col-2">排名</li>
          <li class="col-4">电影名</li>
          <li class="col-3">实时票房(万)</li>
          <li class="col-3">累计票房(万)</li>
      </ul>
      <p v-if="!flag" style="color:white">数据正在加载中。。。</p>
          <ul class="row list-unstyled list-content content-position bg-color box-shadow" v-for="key in movie" v-if="flag">
              <li class="col-2"><span class="bg-warning text-radius">{{key.Irank}}</span></li>
              <li class="col-4">{{key.MovieName | msg}}</li>
              <li class="col-3 text-red">{{key.BoxOffice}}</li>
              <li class="col-3">{{key.sumBoxOffice}}</li>
          </ul>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data:function(){
      return {
         movie:{} ,
          flag:false
      }
    },
  created:function(){
        this.$http.jsonp("https://api.shenjian.io/?appid=f2a730eb54d4ee91a24fce84f8353e59").then(function (result) {
            if(result.body!=0){
                this.movie=[{"Irank":"数据加载失败，请稍后重试"}];
                this.flag=true;
            }
            var data=result.body.data.slice(0,10);
            this.movie=data;
            this.flag=true;
        });
    },
    filters:{
      msg:function (dat) {
         var arr=dat.split("：");
         return arr[0];
      }
    }
}
</script>

